import React, {useEffect} from "react";
import {View} from "native-base";
import App from "../../App";

const privacyPolicyJson = {
	"content": "<div class=\"ce_text privacy_item general_part1 block\"><h1>Datenschutzerklärung</h1><div class=\"text\"><p>Mit dieser Datenschutzerklärung möchten wir Sie über Art, Umfang und Zweck der Verarbeitung von personenbezogenen Daten (im Folgenden auch nur als \"Daten\" bezeichnet) aufklären. Personenbezogene Daten sind alle Daten, die einen persönlichen Bezug zu Ihnen aufweisen, z. B. Name, Adresse, E-Mail-Adresse oder Ihr Nutzerverhalten. Die Datenschutzerklärung gilt für alle von uns vorgenommene Daten-Verarbeitungsvorgänge sowohl im Rahmen unserer Kerntätigkeit als auch für die von uns vorgehaltenen Online-Medien.</p></div></div><div class=\"ce_text privacy_item contact_responsible block\"><div class=\"text\"><h2>Wer bei uns für die Datenverarbeitung verantwortlich ist</h2><p>Verantwortlich für die Datenverarbeitung ist:</p></div></div><p>Universität Osnabrpck<br>Neuer Graben 29 / Schloss<br>49074 Osnabrück<br>Deutschland<br>+49 541 969 0<br></p><div class=\"ce_text privacy_item contact_dataprotectioncommissioner block\"><div class=\"text\"><h2>Kontaktdaten unseres Datenschutzbeauftragten</h2><p>Unseren Datenschutzbeauftragten können Sie per E-Mail unter datenschutzbeauftragter@uni-osnabrueck.de oder unter unserer Postadresse mit dem Zusatz „an den Datenschutzbeauftragten“ erreichen.</p></div></div><div class=\"ce_text privacy_item general_part3 block\"><div class=\"text\"><h2>Ihre Rechte nach der DSGVO</h2><p>Nach der DSGVO stehen Ihnen die nachfolgend aufgeführten Rechte zu, die Sie jederzeit bei dem in Ziffer 1. dieser Datenschutzerklärung genannten Verantwortlichen geltend machen können:</p><ul><li><strong>Recht auf Auskunft:</strong> Sie haben das Recht, von uns Auskunft darüber zu verlangen, ob und welche Daten wir von Ihnen verarbeiten.</li><li><strong>Recht auf Berichtigung:</strong> Sie haben das Recht, die Berichtigung unrichtiger oder Vervollständigung unvollständiger Daten zu verlangen.</li><li><strong>Recht auf Löschung:</strong> Sie haben das Recht, die Löschung Ihrer Daten zu verlangen.</li><li><strong>Recht auf Einschränkung:</strong> Sie haben in bestimmten Fällen das Recht zu verlangen, dass wir Ihre Daten nur noch eingeschränkt bearbeiten.</li><li><strong>Recht auf Datenübertragbarkeit:</strong> Sie haben das Recht zu verlangen, dass wir Ihnen oder einem anderen Verantwortlichen Ihre Daten in einem strukturierten, gängigen und maschinenlesebaren Format übermitteln.</li><li><strong>Beschwerderecht</strong>: Sie haben das Recht, sich bei einer Aufsichtsbehörde zu beschweren. Zuständig ist die Aufsichtsbehörde Ihres üblichen Aufenthaltsortes, Ihres Arbeitsplatzes oder unseres Firmensitzes.</li></ul><h3>Widerrufsrecht</h3><p>Sie haben das Recht, die von Ihnen erteilte Einwilligung zur Datenverarbeitung jederzeit zu widerrufen.</p><h3>Widerspruchsrecht</h3><p>Sie haben das Recht, jederzeit gegen die Verarbeitung Ihrer Daten, die wir auf unser berechtigtes Interesse nach Art. 6 Abs. 1 lit. f DSGVO stützen, Widerspruch einzulegen. Sofern Sie von Ihrem Widerspruchsrecht Gebrauch machen, bitten wir Sie um die Darlegung der Gründe. Wir werden Ihre personenbezogenen Daten dann nicht mehr verarbeiten, es sei denn, wir können Ihnen gegenüber nachweisen, dass zwingende schutzwürdige Gründe an der Datenverarbeitung Ihre Interessen und Rechte überwiegen.</p><p><span style=\"text-decoration: underline;\"><strong>Unabhängig vom vorstehend Gesagten, haben Sie das jederzeitige Recht, der Verarbeitung Ihrer personenbezogenen Daten für Zwecke der Werbung und Datenanalyse zu widersprechen.</strong></span></p><p>Ihren Widerspruch richten Sie bitte an die oben angegebene Kontaktadresse des Verantwortlichen.</p><h2>Wann löschen wir Ihre Daten?</h2><p>Wir löschen Ihre Daten dann, wenn wir diese nicht mehr brauchen oder Sie uns dies vorgeben. Dass bedeutet, dass - sofern sich aus den einzelnen Datenschutzhinweisen dieser Datenschutzerklärung nichts anderes ergibt - wir Ihre Daten löschen,</p><ul><li>wenn der Zweck der Datenverarbeitung weggefallen ist und damit die jeweilige in den einzelnen Datenschutzhinweisen genannte Rechtsgrundlage nicht mehr besteht, also bspw.<ul><li>nach Beendigung der zwischen uns bestehenden vertraglichen oder mitgliedschaftlichen Beziehungen (Art. 6 Abs. 1 lit. a DSGVO) oder</li><li>nach Wegfall unseres berechtigten Interesses an der weiteren Verarbeitung oder Speicherung Ihrer Daten (Art. 6 Abs. 1 lit. f DSGVO),</li></ul></li><li>wenn Sie von Ihrem Widerrufsrecht Gebrauch machen und keine anderweitige gesetzliche Rechtsgrundlage für die Verarbeitung im Sinne von Art. 6 Abs. 1 lit. b-f DSGVO eingreift,</li><li>wenn Sie vom Ihrem Widerspruchsrecht Gebrauch machen und der Löschung keine zwingenden schutzwürdigen Gründe entgegenstehen.</li></ul><p>Sofern wir (bestimmte Teile) Ihre(r) Daten jedoch noch für andere Zwecke vorhalten müssen, weil dies etwa steuerliche Aufbewahrungsfristen (in der Regel 6 Jahre für Geschäftskorrespondenz bzw. 10 Jahre für Buchungsbelege) oder die Geltendmachung, Ausübung oder Verteidigung von Rechtsansprüchen aus vertraglichen Beziehungen (bis zu vier Jahren) erforderlich machen oder die Daten zum Schutz der Rechte einer anderen natürlichen oder juristischen Person gebraucht werden, löschen wir (den Teil) Ihre(r) Daten erst nach Ablauf dieser Fristen. Bis zum Ablauf dieser Fristen beschränken wir die Verarbeitung dieser Daten jedoch auf diese Zwecke (Erfüllung der Aufbewahrungspflichten).</p></div></div><div class=\"ce_text privacy_item general_part2 block\"><div class=\"text\"><h2>Cookies</h2><p>Unsere Internetseite nutzt Cookies. Bei Cookies handelt es sich um kleine Textdateien, bestehend aus einer Reihe von Zahlen und Buchstaben, die auf dem von Ihnen genutzten Endgerät abgelegt und gespeichert werden. Cookies dienen vorrangig dazu, Informationen zwischen dem von Ihnen genutzten Endgerät und unserer Webseite auszutauschen. Hierzu gehören u. a. die Spracheinstellungen auf einer Webseite, der Login-Status oder die Stelle, an der ein Video geschaut wurde.</p><p>Beim Besuch unserer Webseiten werden zwei Typen von Cookies eingesetzt:</p><ul><li><strong>Temporäre Cookies (Session Cookies):</strong> Diese speichern eine sogenannte Session-ID, mit welcher sich verschiedene Anfragen Ihres Browsers der gemeinsamen Sitzung zuordnen lassen. Die Session-Cookies werden gelöscht, wenn Sie sich ausloggen oder Ihren Browser schließen.</li><li><strong>Permanente Cookies: </strong>Permanente Cookies bleiben auch nach dem Schließen des Browsers gespeichert. Dadurch erkennt unsere Webseite Ihren Rechner wieder, wenn Sie auf unsere Webseite zurückkehren. In diesen Cookies werden beispielsweise Informationen zu Spracheinstellungen oder Log-In-Informationen gespeichert. Außerdem kann mit diesen Cookies Ihr Surfverhalten dokumentiert und gespeichert werden. Diese Daten können zu Statistik-, Marketing- und Personalisierungs-Zwecken verwendet werden.</li></ul><p>Neben der vorstehenden Einteilung können Cookies auch im Hinblick auf ihren Einsatzzweck unterschieden werden:</p><ul><li><strong>Notwendige Cookies:</strong> Dies sind Cookies, die für den Betrieb unserer Webseite unbedingt erforderlich sind, um Logins oder Warenkörbe für die Dauer Ihrer Sitzung zu speichern oder Cookies, die aus Sicherheitsgründen gesetzt werden.</li><li><strong>Statistik-, Marketing- und Personalisierungs-Cookies:</strong> Dies sind Cookies, die für Analysezwecke oder die Reichweitenmessung eingesetzt werden. Über solche \"Tracking\"-Cookies können insbesondere Informationen zu eingegebenen Suchbegriffen oder die Häufigkeit von Seitenaufrufen gespeichert sein. Daneben kann aber auch das Surfverhalten eines einzelnen Nutzers (z. B. Betrachten bestimmter Inhalte, Nutzen von Funktionen etc.) in einem Nutzerprofil gespeichert werden. Solche Profile werden genutzt, um Nutzern Inhalte anzuzeigen, die deren potentiellen Interessen entsprechen. Soweit wir Dienste nutzen, über die Cookies zu Statistik-, Marketing- und Personalisierungs-Zwecken auf Ihrem Endgerät gespeichert werden, informieren wir Sie hierzu gesondert in den nachfolgenden Abschnitten unserer Datenschutzerklärung oder im Rahmen der Einholung Ihrer Einwilligung.</li></ul><p><strong>Betroffene Daten:</strong></p><ul><li>Nutzungsdaten (bspw. Zugriffszeiten, angeklickte Webseiten)</li><li>Kommunikationsdaten (bspw. Informationen über das genutzte Gerät, IP-Adresse).</li></ul><p><strong>Betroffene Personen: </strong>Nutzer unserer Onlineangebote</p><p><strong>Verarbeitungszweck: </strong>Ausspielen unserer Internetseiten, Gewährleistung des Betriebs unserer Internetseiten, Verbesserung unseres Internetangebotes, Kommunikation und Marketig</p><p><strong>Rechtsgrundlage:<br>Berechtigtes Interesse, Art. 6 Abs. 1 lit. f DSGVO</strong><br>Sofern wir von Ihnen keine Einwilligung in das Setzen der Cookies einholen, stützen wir die Verarbeitung Ihrer Daten auf unser berechtigtes Interesse, die Qualität und Benutzerfreundlichkeit unseres Internetauftritts, insbesondere der Inhalte und Funktionen zu verbessern. Sie haben über die Sicherheitseinstellungen Ihres Browsers, dem Einsatz der von uns im Rahmen unseres berechtigten Interesses gesetzten Cookies zu widersprechen. Dort haben Sie die Möglichkeit festzulegen, ob Sie etwa von vornherein keine oder nur auf Nachfrage Cookies akzeptieren oder aber festlegen, dass Cookies nach jedem Schließen Ihres Browsers gelöscht werden. Werden Cookies für unsere Webseite deaktiviert, können möglicherweise nicht mehr alle Funktionen der Webseite vollumfänglich genutzt werden.</p></div></div><div class=\"ce_text privacy_item registry block\"><div class=\"text\"><h2>Registrierung, Anmeldung und Nutzerkonto</h2><p>Sie haben die Möglichkeit sich auf unserem Online-Medium zu registrieren, um dort ein Nutzerkonto anzulegen. Hierzu ist die Angabe personenbezogener Daten notwendig, die sich aus der Eingabemaske ergeben. Zu den dort abgefragten Daten gehören insbesondere Ihr Name, Ihre E-Mail-Adresse, ggfls. ein Nutzername sowie das Passwort. Diese Daten werden von uns gespeichert und verarbeitet, um für Sie ein Nutzerkonto einzurichten und die (wiederholte) Anmeldung zu ermöglichen. Die Daten können jederzeit durch Sie geändert oder gelöscht werden. Die Daten werden nicht an Dritte weitergegeben außer dies dient der technischen und organisatorischen Abwicklung des zwischen uns bestehenden Nutzungsvertrages. Um Sie und uns vor missbräuchlichen Registrierungen zu schützen, speichern wir die Ihnen im Zeitpunkt der Registrierung zugeordnete IP-Adresse, sowie das Datum und die Uhrzeit der Registrierung.</p><p><strong>Betroffene Daten:</strong></p><ul><li>Bestandsdaten (bspw. Namen, Adressen)</li><li>Kontakdaten (bspw. E-Mail-Adresse, Telefonnummer, Postanschrift)</li><li>Vertragsdaten (bspw. Vertragsgegenstand, Vertragsdauer)</li><li>Zahlungsdaten (bspw. Bankverbindungsdaten, Rechnungen)</li><li>Inhaltsdaten (bspw. Posts, Fotos, Videos)</li><li>Nutzungsdaten (bspw. Zugriffszeiten, angeklickte Webseiten)</li><li>Kommunikationsdaten (bspw. Informationen über das genutzte Gerät, IP-Adresse)</li></ul><p><strong>Verarbeitungszweck: </strong>Abwicklung vertraglicher Leistungen, Kommunikation sowie Beantwortung von Kontaktanfragen, Sicherheitsmaßnahmen.</p><p><strong>Rechtsgrundlage:</strong> Vertragserfüllung und vorvertragliche Anfragen, Art. 6 Abs. 1 lit. b DSGVO, rechtliche Verpflichtung, Art. 6 Abs. 1 lit. c DSGVO, berechtigtes Interesse, Art. 6 Abs. 1 lit. f DSGVO</p><p><strong>Löschung:</strong> Siehe hierzu den Punkt: <strong>\"Wann löschen wir Ihre Daten?\"</strong>. Daneben möchten wir Sie darauf aufmerksam machen, dass wir die im Rahmen der Registrierung erhobenen Daten sowie die in dem Account hinterlegten Inhaltsdaten vorbehaltlich entgegenstehender gesetzlicher Aufbewahrungspflichten löschen, sobald Sie Ihren Account löschen. Wir bitten Sie daher, sofern Sie auf die in Ihrem Account hinterlegten Inhaltsdaten auch nach der Löschung Ihres Account zugreifen wollen oder müssen, diese vor der Löschung des Accounts anderweitig zu sichern.</p></div></div><div class=\"ce_text privacy_item blog block\"><div class=\"text\"><h2>Ihre Kommentare oder Bewertungen</h2><p>Sie haben die Möglichkeit, auf den dafür vorgesehenen Bereichen unserer Internetpräsenz unsere Beiträge zu kommentieren, Ihre Meinung zu äußern oder Content einzustellen. Da wir für rechtsverletzende Inhalte Ihres Kommentars (Beleidigung, Schmähkritik, Volksverhetzung, verbotene Gewaltdarstellung etc.) unter Umständen selbst in Haftung genommen werden können, speichern wir Ihre IP-Adresse für die Dauer von 7 Tagen, um gegebenenfalls hierüber Ihre Identität ermitteln zu können.</p><p><strong>Rechtsgrundlage</strong>: Art. 6 Abs. 1 lit. f DSGVO</p><p><strong>Betroffene Daten:</strong></p><ul><li>Bestandsdaten (bspw. Namen, Adressen)</li><li>Inhaltsdaten (bspw. Posts, Fotos, Videos)</li><li>Nutzungsdaten (bspw. Zugriffszeiten, angeklickte Webseiten)</li><li>Kommunikationsdaten (bspw. Informationen über das genutzte Gerät, IP-Adresse)</li></ul><p><strong>Verarbeitungszweck: </strong>Leistungserfüllung vertraglicher Verpflichtungen, Kommunikation und Abwicklung von Anfragen, Einholen von Feedback, Sicherheitsmaßnahmen.</p><p></p></div></div><div class=\"ce_text block\"><div class=\"text\"><h2>Chat-Dienste</h2><p>Wir kommunizieren über Online-Chats und/oder Chatbots (nachfolgend als Chat-Dienste bezeichnet). Unter einem Online-Chat versteht man den Austausch von Text-Nachrichten über das Internet in Echtzeit. Chat-Bots sind Computer-Programme, die in der Regel auf Internetseiten eingesetzt werden, um Nutzerfragen automatisiert zu beantworten oder Benachrichtigungen an Nutzer auszuspielen.</p><p>Sofern Sie unsere Chat-Dienste nutzen, erheben wir Ihre Zugriffs-Daten. Hierzu gehören insbesondere die IP-Adresse des anfragenden Geräts, das Datum und die Uhrzeit der Anfrage sowie Ihre Identifikationsnummer. Ferner speichern wir den Inhalt der über die Chat-Dienste geführte Kommunikation. Schließlich protokollieren wir Ihre Registrierung und Ihre Einwilligungserklärung. Diese Daten nutzen wir, um mit Ihnen über unsere Chat-Dienste kommunizieren zu können, Sie persönlich anzusprechen, um Ihre Anfragen zu beantworten sowie etwaige angeforderte Inhalte zu übermitteln.</p><p>Der oder die unten genannte/n Diensteanbieter hat/haben die Möglichkeit, auf die Metadaten der Kommunikation zuzugreifen. Hierzu gehören Zeit und (je nach Einstellung) Ort der Kommunikation sowie das von Ihnen verwendete Gerät sowie die Information wer mit wem kommuniziert hat. Der Zugriff auf die Metadaten durch den Diensteanbieter erfolgt in der Regel um den Dienst zu optimieren oder aus Sicherheitsgründen. Daneben kann der Zugriff durch den Diensteanbieter aber auch erfolgen, um die Daten zu Marketingzwecken einzusetzen. Näheres hierzu erfahren Sie in der Datenschutzerklärung des Diensteanbieters, die wir nachfolgend verlinkt haben.</p><p>Wir möchten Sie darauf hinweisen, dass je nach Sitzland des unten genannten Anbieters die über dessen Plattform erfassten Daten außerhalb des Raumes der Europäischen Union übertragen und verarbeitet werden können. Es besteht in diesem Fall das Risiko, dass das von der DSGVO vorgeschriebene Datenschutzniveau nicht eingehalten und die Durchsetzung Ihrer Rechte nicht oder nur erschwert erfolgen kann.</p><p><strong>Betroffene Daten:</strong></p><ul><li>Bestands- und Kontaktdaten (bspw. Name, Telefonnummer, E-Mail-Adresse)</li><li>Inhaltsdaten (bspw. Twexteingaben)</li><li>Nutzungsdaten (bspw. Zugriffszeiten, Interesse an Inhalten)</li><li>Kommunikationsdaten (bspw. Informationen über das genutzte Gerät, IP-Adresse).</li></ul><p><strong>Verarbeitungszweck: </strong>Kommunikation und Marketing</p><p><strong>Rechtsgrundlage:</strong> Sofern wir Sie vor dem Einsatz des jeweiligen Chat-Dienstes um Ihre Einwilligung gebeten haben, liegt hierin die Rechtsgrundlage, Art. 6 Abs. 1 lit. a DSGVO. Insofern stellen wir klar, dass wir die Ihre Kontaktdaten ohne Ihre Einwilligung nicht erstmalig an den Diensteanbieter übermitteln. Sofern wir mit Ihnen im Rahmen einer Vertragsanbahnung oder im Rahmen eines bestehenden vertraglichen Verhältnisses über einen der nachfolgenden genannten Dienste kommunizieren, besteht die Rechtsgrundlage in der Erfüllung oder Vorbereitung des Vertrages, Art. 6 Abs. 1 lit. b DSGVO. Im Übrigen berufen wir uns auf unsere berechtigten Interessen an einer schnellen und effizienten Kommunikation und Erfüllung der Bedürfnisse unser Kommunikationspartner an der Kommunikation der nachfolgend genannten Dienste, Art. 6 Abs. 1 lit. f DSGVO.</p><p><strong>Widerspruchsmöglichkeiten: </strong>Die uns zur Nutzung des nachfolgenden genannten Dienstes erteilte Einwilligung können Sie jederzeit widerrufen. Der Verarbeitung Ihrer Daten über die von uns genutzten Chat-Dienst können Sie zudem jederzeit widersprechen.</p><p><strong>Wir nutzen folgende Chatbots und Chatfunktionen:</strong></p></div></div><div class=\"ce_text privacy_item general_part4 block\"><div class=\"text\"><h2>Sicherheitsmaßnahmen</h2><p>Wir treffen im Übrigen technische und organisatorische Sicherheitsmaßnahmen nach dem Stand der Technik, um die Vorschriften der Datenschutzgesetze einzuhalten und Ihre Daten gegen zufällige oder vorsätzliche Manipulationen, teilweisen oder vollständigen Verlust, Zerstörung oder gegen den unbefugten Zugriff Dritter zu schützen.</p><h2>Aktualität und Änderung dieser Datenschutzerklärung</h2><p>Diese Datenschutzerklärung ist aktuell gültig und hat den Stand Dezember 2021. Aufgrund geänderter gesetzlicher bzw. behördlicher Vorgaben kann es notwendig werden, diese Datenschutzerklärung anzupassen.</p><p><strong>Diese Datenschutzerklärung wurde mit Hilfe des Datenschutz-Generators von SOS Recht erstellt. SOS Recht ist ein Angebot der Mueller.legal Rechtsanwälte Partnerschaft mit Sitz in Berlin.</strong></p></div></div>"
}
